import "../public/css/login.css" 
const loginForm = document.getElementById('login-btn');
const loginBtn = document.getElementsByClassName('login-btn')[0];
const getTokenBtn = document.getElementById("getToken")
const userInput = document.getElementById("username")
let countDownIndex = 60
loginBtn.addEventListener('click', function (e) {
    var e = e || window.event;
    // 阻止默认事件
    e.preventDefault();
    var params = {
        mobilePhone: loginForm.elements.username.value,
        code: loginForm.elements.password.value
    };
    fetch("https://rental.hellobike.com/partner/company/login", {
        method: 'POST',
        body: JSON.stringify(params),
        headers: {
            token: null,
            "Content-Type": "application/json;charset=UTF-8"
        }
    }).then(data=>data.json()).then(data=>{
        if(data.status === 1){
            sessionStorage.setItem("token", data.data)
            location.href = "../index.html"
        }else{
            alert(data.info)
        }
    })
})

getTokenBtn.addEventListener('click', function (e) {
    var e = e || window.event
    e.preventDefault() //阻止默认事件
    var params = {
        mobilePhone: loginForm.elements.username.value
    };
    console.log(params.mobilePhone.match(/\^1\d{13}/))
    getTokenBtn.setAttribute("disabled", "disabled")
    getTokenBtn.classList.add("active")
    fetch("https://rental.hellobike.com/partner/company/sendSmsCode", {
        method: 'POST',
        body: JSON.stringify(params),
        headers: {
            token: null,
            "Content-Type": "application/json;charset=UTF-8"
        }
    }).then(data => {
        return data.json()
    }).then(data => {
        if (data.status === 1) {
            alert("发送成功")
            let timer = setInterval(() => {
                getTokenBtn.innerHTML = countDownIndex - 1 + 's'
                countDownIndex--
                if (countDownIndex === 0) {
                    clearInterval(timer)
                    getTokenBtn.removeAttribute("disabled")
                    getTokenBtn.classList.remove("active")
                    getTokenBtn.innerHTML = "发送验证码"
                    countDownIndex = 60
                }
            }, 1000)
        } else {
            alert(data.info)
            getTokenBtn.removeAttribute("disabled")
            getTokenBtn.classList.remove("active")
        }
    })
})

userInput.addEventListener('change', ()=>{
    if(validation(userInput.value)){
        userInput.classList.remove("error")
        userInput.classList.add("correct")
        getTokenBtn.removeAttribute("disabled")
        getTokenBtn.classList.remove("active")
    }
    else{
        userInput.classList.remove("correct")
        userInput.classList.add("error")
    }
})

function validation(phone){
    if(phone.length !== 11 || isNaN(phone) || !/^1\d{10}/.test(phone)){
        return false
    }
    return true
}
